import React, { Component, useEffect, useState } from 'react';
import './Style/Yangiliklar.scss'
import axios from "axios";
const url = window.location.origin;


function Yangiliklar() {
  const [posts, setPosts] = useState([]);

  const updateAllData = (function () {
    axios({
      url: url + "/posts",
      method: 'GET',
      responseType: "json"
    })
      .then(data => {
        console.log(data);
        data.data = data.data.sort((a, b) => {  return new Date(b.createdPostDate) -  new Date(a.createdPostDate) })
        setPosts(() => { return data.data });
      })
      .catch(e => {
        console.log(e);
      })
  })

  useEffect(updateAllData, [])
  console.log(posts)
  return (
    <div className='news-card'>
      {
        posts.map((post) => {
          return <div className='news-card__item' key={post._id}>
            <div className='news-card__boxImg'>
              <img src={url + "/posts/getImage/" + post.imageId} alt={post.caption} />
            </div>
            <div className="context-text">
              <h3>{post.caption}</h3>
              <p> {post.description}</p>
              <hr />
              <div className='data'>
                <span>{post.createdPostDate}</span>
              </div>
            </div>
          </div>
        })
      }

    </div>
  )
}

export default Yangiliklar


